header {
  width: 100%;
  margin-top: -1.5rem;
}

.containerHeader {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

h1.titlePortfolio{
  padding-bottom: 25px;
}
.hero {
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  .heroImage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.subBar {
  background: rgb(80 80 80 / 30%);
  height: 5px;
}

.subTitle {
  font-size: 1.4rem;
  font-weight: 400;
}



.box {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 480px;
  height: 300px;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0 20px 50px rgb(31, 32, 41);
  color: white;
  padding: 20px;
}

.box:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  transition: 0.5s;
  pointer-events: none;
}

.box:hover:before {
  left: -50%;
  transform: skewX(-5deg);
}


.box .content {
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  padding: 40px;
  text-align: center;
  background: #000;
    opacity: 0.8;
  box-shadow: 0 5px 10px rgba(9, 0, 0, 0.5);

}

.box span {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
  right: 0;
  padding: 50px;
  z-index: 1;
}

.box span:nth-child(1) {
  transform: rotate(0deg);
}

.box span:nth-child(2) {
  transform: rotate(90deg);
}

.box span:nth-child(3) {
  transform: rotate(80deg);
}

.box span:nth-child(4) {
  transform: rotate(0deg);
}

.box span:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background: #6d0000;
  animation: animate 4s linear infinite;
}

@keyframes animate {
  0% {
    transform: scaleX(0);
    transform-origin: left;
  }

  50% {
    transform: scaleX(1);
    transform-origin: left;
  }

  50.1% {
    transform: scaleX(1);
    transform-origin: right;

  }

  100% {
    transform: scaleX(0);
    transform-origin: right;
  }
}


/* ================ MEDIA QUERIES (MEDIUM DEVICES) ================= */
@media screen and (max-width: 1024px) {
  header {
    padding-top: 1rem;
  }

  .box {
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 350px;
    height: 200px;
  }
}

/* ================ MEDIA QUERIES (SMALL DEVICES) ================= */
@media screen and (max-width: 600px) {

  .box {
    top: 100%;
    height: 260px;
    position: relative;
  }

  .subBar {
    display: none;
  }
}